.create-event-form {
  position: relative;
  display: grid;
  justify-content: center;
  align-items: center;
}
.create-event-form input {
  text-align: center;
  font-size: 1.2rem;
  color: black;
  outline: none;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.689);
  padding: 1.4rem 0;
  background-color: transparent;
}
.create-event-form input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.634);
}
.create-event-form input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.634);
}
.create-event-form input::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.634);
}
.create-event-form input::placeholder {
  color: rgba(0, 0, 0, 0.634);
}
.create-event-form input:focus::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.868);
}
.create-event-form input:focus:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.868);
}
.create-event-form input:focus::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.868);
}
.create-event-form input:focus::placeholder {
  color: rgba(0, 0, 0, 0.868);
}
.create-event-form input.time {
  width: 130px;
  margin: 0 auto;
  padding-left: 35px;
  border: none;
  -webkit-animation: navigate 2s infinite;
          animation: navigate 2s infinite;
}
@-webkit-keyframes navigate {
  /* 0 & 100% might not be needed */
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes navigate {
  /* 0 & 100% might not be needed */
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.create-event-form input.time:focus {
  border: none;
  -webkit-animation: none;
          animation: none;
}
.create-event-form .submit-btn {
  /* border: 2px solid black; */
  border-radius: 3px;
  outline: none;
  background-color: black;
  color: white;
  font-size: 1em;
  width: 100%;
  padding: 0.5rem 0;
  cursor: pointer;
}
.create-event-form .submit-btn:hover {
  background-color: transparent;
  color: black;
  transition: all 0.1s ease-in-out;
}
.create-event-form .submit-btn:active {
  background-color: white;
  color: #17c58c;
}

